'use client'

import React, { useState, useEffect, useCallback, useMemo, useRef } from 'react';

// Tailwind CSS is assumed to be set up in the project.
// For custom colors, we'll use JIT mode's arbitrary value support like `bg-[#260801]`.

// Constants & Configuration
const GRID_SIZE = 8;
const NUM_PIECE_TYPES = 5;
const INITIAL_PLAYER_HEALTH = 100;
const INITIAL_ENEMY_HEALTH = 100;
const TIMELINE_VISIBLE_TURNS = 5; // Number of future turns visible on timeline
const DESKTOP_FIXED_WIDTH = '1600px';
const MOBILE_BREAKPOINT = 768; // px

// Color Palette (as comments for reference, used directly in Tailwind classes)
// #F2EB80 (LightYellow)
// #BF6A1F (MidOrangeBrown)
// #401A04 (DarkBrownTextOrPanelBg)
// #8C441B (MediumBrownAccents)
// #260801 (VeryDarkBrownBg)

const COLORS = {
  bgVeryDark: 'bg-[#260801]',
  bgPanel: 'bg-[#401A04]',
  textLight: 'text-[#F2EB80]',
  textDark: 'text-[#401A04]', // For light backgrounds, if any
  accentOrangeBrown: 'bg-[#BF6A1F]',
  borderMediumBrown: 'border-[#8C441B]',
  highlightYellow: 'text-[#F2EB80]',
  shadowColor: 'shadow-[0_4px_14px_rgba(242,235,128,0.1)]', // Custom shadow with yellow tint
};

const PIECE_COLORS = [
  'bg-red-500',
  'bg-blue-500',
  'bg-green-500',
  'bg-yellow-500',
  'bg-purple-500',
];

// Types and Interfaces
interface PuzzlePiece {
  id: string;
  type: number; // 0 to NUM_PIECE_TYPES - 1
  color: string; // Tailwind color class
}

interface TimelineAction {
  id: string;
  turnScheduled: number;
  description: string;
  actor: 'player' | 'enemy';
  type: 'attack' | 'heal' | 'buff' | 'debuff' | 'special';
  value: number;
  icon: React.ReactNode; // SVG or emoji
  resolved: boolean;
}

interface GameStateSnapshot {
  currentTurn: number;
  playerHealth: number;
  enemyHealth: number;
  puzzleGrid: PuzzlePiece[][];
  playerTimeline: TimelineAction[];
  enemyTimeline: TimelineAction[];
  gameStatus: 'playing' | 'player_won' | 'enemy_won';
}

interface GameState extends GameStateSnapshot {
  maxPlayerHealth: number;
  maxEnemyHealth: number;
  selectedPiece: { r: number; c: number } | null;
  comboChain: { r: number; c: number }[];
  isProcessingTurn: boolean; // For animations and disabling input
  history: GameStateSnapshot[];
  replayMode: boolean;
  replayTurnIndex: number;
  previewedAction: TimelineAction | null;
  isMobileView: boolean;
  leftPanelWidth: number; // For draggable splitter
}

// Utility Functions
const createId = () => Math.random().toString(36).substr(2, 9);

const generateRandomPiece = (): PuzzlePiece => {
  const type = Math.floor(Math.random() * NUM_PIECE_TYPES);
  return {
    id: createId(),
    type,
    color: PIECE_COLORS[type],
  };
};

const initializeGrid = (): PuzzlePiece[][] => {
  return Array(GRID_SIZE)
    .fill(null)
    .map(() => Array(GRID_SIZE).fill(null).map(generateRandomPiece));
};

// --- Reusable UI Components ---

interface HealthBarProps {
  currentHealth: number;
  maxHealth: number;
  label: string;
  barColor: string; // Tailwind bg color class
  bgColor?: string;
}

const HealthBar: React.FC<HealthBarProps> = ({ currentHealth, maxHealth, label, barColor, bgColor = 'bg-gray-700' }) => {
  const percentage = Math.max(0, (currentHealth / maxHealth) * 100);
  return (
    <div className="w-full">
      <div className="flex justify-between mb-1">
        <span className={`${COLORS.textLight} text-sm font-medium`}>{label}</span>
        <span className={`${COLORS.textLight} text-sm font-medium`}>{currentHealth} / {maxHealth}</span>
      </div>
      <div className={`w-full ${bgColor} rounded-full h-4 ${COLORS.borderMediumBrown} border`}>
        <div
          className={`${barColor} h-4 rounded-full transition-all duration-500 ease-out`}
          style={{ width: `${percentage}%` }}
        />
      </div>
    </div>
  );
};

interface TimelineProps {
  actions: TimelineAction[];
  currentTurn: number;
  title: string;
  isPlayerTimeline: boolean;
  onHoverAction: (action: TimelineAction | null) => void;
}

const Timeline: React.FC<TimelineProps> = ({ actions, currentTurn, title, isPlayerTimeline, onHoverAction }) => {
  const pathRef = useRef<SVGPathElement>(null);
  const [pathLength, setPathLength] = useState(0);

  useEffect(() => {
    if (pathRef.current) {
      setPathLength(pathRef.current.getTotalLength());
    }
  }, [pathRef.current]); // Re-run if pathRef changes (e.g. on resize if SVG is responsive)

  const getPositionOnPath = (turnOffset: number) => {
    if (!pathRef.current || pathLength === 0) return { x: 0, y: 0 };
    // Normalize turnOffset to be within visible range, e.g., 0 to TIMELINE_VISIBLE_TURNS
    // Let's say 0 is current turn, positive is future.
    const normalizedProgress = Math.min(Math.max(turnOffset, 0), TIMELINE_VISIBLE_TURNS -1) / (TIMELINE_VISIBLE_TURNS -1);
    const point = pathRef.current.getPointAtLength(normalizedProgress * pathLength);
    return { x: point.x, y: point.y };
  };

  const displayedActions = actions
    .filter(action => !action.resolved && action.turnScheduled >= currentTurn && action.turnScheduled < currentTurn + TIMELINE_VISIBLE_TURNS)
    .sort((a, b) => a.turnScheduled - b.turnScheduled);

  return (
    <div className={`p-4 ${COLORS.bgPanel} rounded-lg ${COLORS.shadowColor} w-full`}>
      <h3 className={`${COLORS.textLight} text-lg font-semibold mb-3 text-center`}>{title}</h3>
      <svg width="100%" height="80" viewBox="0 0 400 80" preserveAspectRatio="xMidYMid meet">
        {/* The motion path. A simple horizontal line for now. */}
        <path
          ref={pathRef}
          d="M 20 40 H 380" // Path from x=20 to x=380 at y=40
          stroke={isPlayerTimeline ? '#BF6A1F' : '#8C441B'}
          strokeWidth="3"
          fill="transparent"
        />
        {/* Current turn marker */}
        <circle cx={getPositionOnPath(0).x} cy={getPositionOnPath(0).y} r="8" fill={COLORS.highlightYellow.replace('text-', '')} stroke="#260801" strokeWidth="2">
            <title>Current Turn</title>
        </circle>
        <text x={getPositionOnPath(0).x} y={getPositionOnPath(0).y + 25} textAnchor="middle" fill={COLORS.highlightYellow.replace('text-', '')} fontSize="12">Now</text>

        {/* Action Icons */}
        {displayedActions.map(action => {
          const turnOffset = action.turnScheduled - currentTurn;
          const { x, y } = getPositionOnPath(turnOffset);
          return (
            <g
              key={action.id}
              transform={`translate(${x}, ${y})`}
              className="transition-transform duration-500 ease-in-out"
              onMouseEnter={() => onHoverAction(action)}
              onMouseLeave={() => onHoverAction(null)}
            >
              <circle r="10" fill={isPlayerTimeline ? '#F2EB80' : '#BF6A1F'} stroke="#401A04" strokeWidth="2" />
              {typeof action.icon === 'string' ? (
                <text x="0" y="4" textAnchor="middle" fontSize="12" fill="#401A04">{action.icon}</text>
              ) : (
                <g transform="translate(-8 -8) scale(0.8)">{action.icon}</g>
              )}
              <title>{`${action.description} (Turn ${action.turnScheduled})`}</title>
            </g>
          );
        })}
      </svg>
      {/* Optional: turn markers text below the path */}
      <div className="flex justify-between mt-1 px-2">
        {Array.from({ length: TIMELINE_VISIBLE_TURNS }).map((_, i) => (
          <span key={i} className={`${COLORS.textLight} text-xs`}>
            {i === 0 ? `T ${currentTurn}` : `T+${i}`}
          </span>
        ))}
      </div>
    </div>
  );
};


// --- Main Game Application Component ---
const PuzzleBattleGame: React.FC = () => {
  const initialGrid = useMemo(() => initializeGrid(), []);
  const [gameState, setGameState] = useState<GameState>(() => ({
    currentTurn: 1,
    playerHealth: INITIAL_PLAYER_HEALTH,
    maxPlayerHealth: INITIAL_PLAYER_HEALTH,
    enemyHealth: INITIAL_ENEMY_HEALTH,
    maxEnemyHealth: INITIAL_ENEMY_HEALTH,
    puzzleGrid: initialGrid,
    playerTimeline: [],
    enemyTimeline: [],
    selectedPiece: null,
    comboChain: [],
    isProcessingTurn: false,
    gameStatus: 'playing',
    history: [],
    replayMode: false,
    replayTurnIndex: 0,
    previewedAction: null,
    isMobileView: window.innerWidth < MOBILE_BREAKPOINT,
    leftPanelWidth: 60, // Percentage for the left panel (puzzle grid)
  }));

  const puzzleGridRef = useRef<HTMLDivElement>(null);
  const abilityBarRef = useRef<HTMLDivElement>(null);
  const splitterRef = useRef<HTMLDivElement>(null);

  // --- Responsive Design Hook ---
  useEffect(() => {
    const handleResize = () => {
      setGameState(prev => ({ ...prev, isMobileView: window.innerWidth < MOBILE_BREAKPOINT }));
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // --- Draggable Splitter Logic ---
  useEffect(() => {
    const splitter = splitterRef.current;
    if (!splitter || gameState.isMobileView) return;

    const handleMouseDown = (e: MouseEvent) => {
      e.preventDefault();
      const startX = e.clientX;
      const gameArea = splitter.parentElement;
      if (!gameArea) return;
      
      const initialLeftWidth = puzzleGridRef.current?.offsetWidth || 0;

      const handleMouseMove = (moveEvent: MouseEvent) => {
        const dx = moveEvent.clientX - startX;
        const totalWidth = gameArea.offsetWidth;
        const newLeftWidthPx = initialLeftWidth + dx;
        
        // Min/max width for panels (e.g., 20% to 80%)
        const minPx = totalWidth * 0.20;
        const maxPx = totalWidth * 0.80;
        
        const clampedLeftWidthPx = Math.max(minPx, Math.min(newLeftWidthPx, maxPx));
        const newLeftPanelPercentage = (clampedLeftWidthPx / totalWidth) * 100;

        setGameState(prev => ({ ...prev, leftPanelWidth: newLeftPanelPercentage }));
      };

      const handleMouseUp = () => {
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
      };

      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
    };

    splitter.addEventListener('mousedown', handleMouseDown);
    return () => {
      splitter.removeEventListener('mousedown', handleMouseDown);
    };
  }, [gameState.isMobileView, splitterRef, puzzleGridRef]);


  // --- Puzzle Logic (Simplified) ---
  const handlePieceClick = useCallback((r: number, c: number) => {
    if (gameState.isProcessingTurn || gameState.replayMode || gameState.gameStatus !== 'playing') return;

    if (gameState.selectedPiece) {
      const { r: r1, c: c1 } = gameState.selectedPiece;
      // Basic swap logic: only adjacent pieces
      if ((Math.abs(r1 - r) === 1 && c1 === c) || (Math.abs(c1 - c) === 1 && r1 === r)) {
        const newGrid = gameState.puzzleGrid.map(row => [...row]);
        [newGrid[r1][c1], newGrid[r][c]] = [newGrid[r][c], newGrid[r1][c1]];
        
        // Simplistic match detection and effect
        // In a real game: detectMatches, handleCascades, generateResources/Actions
        let matchFound = false;
        const checkMatch = (nr: number, nc: number): boolean => {
            const piece = newGrid[nr][nc];
            // Horizontal match
            if (nc > 1 && newGrid[nr][nc-1].type === piece.type && newGrid[nr][nc-2].type === piece.type) return true;
            if (nc < GRID_SIZE-2 && newGrid[nr][nc+1].type === piece.type && newGrid[nr][nc+2].type === piece.type) return true;
            if (nc > 0 && nc < GRID_SIZE-1 && newGrid[nr][nc-1].type === piece.type && newGrid[nr][nc+1].type === piece.type) return true;
            // Vertical match
            if (nr > 1 && newGrid[nr-1][nc].type === piece.type && newGrid[nr-2][nc].type === piece.type) return true;
            if (nr < GRID_SIZE-2 && newGrid[nr+1][nc].type === piece.type && newGrid[nr+2][nc].type === piece.type) return true;
            if (nr > 0 && nr < GRID_SIZE-1 && newGrid[nr-1][nc].type === piece.type && newGrid[nr+1][nc].type === piece.type) return true;
            return false;
        }

        if (checkMatch(r,c) || checkMatch(r1,c1)) {
            matchFound = true;
            // Simplistic "combo" effect - highlight swapped pieces
            const combo: {r:number, c:number}[] = [{r,c}, {r1,c1}];
            // In a real game, this would trace the actual matched pieces.
            setGameState(prev => ({ ...prev, comboChain: combo}));
            setTimeout(() => setGameState(prev => ({ ...prev, comboChain: []})), 500);


            // Simulate generating an action from a match
            const playerAttackAction: TimelineAction = {
              id: createId(),
              turnScheduled: gameState.currentTurn + 1, // Action for next turn
              description: "Puzzle Attack",
              actor: 'player',
              type: 'attack',
              value: 10, // Damage value
              icon: '⚔️',
              resolved: false,
            };
            setGameState(prev => ({
              ...prev,
              puzzleGrid: newGrid.map(row => row.map(p => (p.type === newGrid[r][c].type || p.type === newGrid[r1][c1].type) && checkMatch(p.id.split('-')[0] as any, p.id.split('-')[1] as any) ? generateRandomPiece() : p)), // Basic refill
              playerTimeline: [...prev.playerTimeline, playerAttackAction],
              selectedPiece: null,
            }));
            // Trigger turn processing after a short delay for visual feedback
            setTimeout(() => processTurn(), 300);
        } else {
            // No match, swap back (or not, depending on game rules)
            setGameState(prev => ({ ...prev, selectedPiece: null })); // Deselect
        }

      } else {
        setGameState(prev => ({ ...prev, selectedPiece: { r, c } }));
      }
    } else {
      setGameState(prev => ({ ...prev, selectedPiece: { r, c } }));
    }
  }, [gameState]);

  const playerAbilities = useMemo(() => [
    { id: 'strong_attack', name: 'Strong Attack (T+2)', cost: 0, effect: () => {
        const action: TimelineAction = { id: createId(), turnScheduled: gameState.currentTurn + 2, description: 'Strong Player Attack', actor: 'player', type: 'attack', value: 25, icon: '💥', resolved: false };
        setGameState(prev => ({ ...prev, playerTimeline: [...prev.playerTimeline, action]}));
        processTurn();
    }},
    { id: 'heal_player', name: 'Heal (T+1)', cost: 0, effect: () => {
        const action: TimelineAction = { id: createId(), turnScheduled: gameState.currentTurn + 1, description: 'Player Heal', actor: 'player', type: 'heal', value: 20, icon: '💖', resolved: false };
        setGameState(prev => ({ ...prev, playerTimeline: [...prev.playerTimeline, action]}));
        processTurn();
    }},
  ], [gameState.currentTurn]);

  const useAbility = (abilityId: string) => {
    if (gameState.isProcessingTurn || gameState.replayMode || gameState.gameStatus !== 'playing') return;
    const ability = playerAbilities.find(ab => ab.id === abilityId);
    if (ability) {
      ability.effect();
    }
  };
  
  // --- Game Loop / Turn Processing ---
  const processTurn = useCallback(() => {
    if (gameState.gameStatus !== 'playing') return;

    setGameState(prev => ({ ...prev, isProcessingTurn: true }));

    // Snapshot current state for history BEFORE modifications for this turn
    const snapshot: GameStateSnapshot = {
      currentTurn: gameState.currentTurn,
      playerHealth: gameState.playerHealth,
      enemyHealth: gameState.enemyHealth,
      puzzleGrid: gameState.puzzleGrid.map(row => row.map(piece => ({...piece}))), // Deep copy
      playerTimeline: gameState.playerTimeline.map(action => ({...action})),
      enemyTimeline: gameState.enemyTimeline.map(action => ({...action})),
      gameStatus: gameState.gameStatus,
    };
    
    let newPlayerHealth = gameState.playerHealth;
    let newEnemyHealth = gameState.enemyHealth;
    let newPlayerTimeline = [...gameState.playerTimeline];
    let newEnemyTimeline = [...gameState.enemyTimeline];

    // 1. Resolve Player Actions for current turn
    newPlayerTimeline = newPlayerTimeline.map(action => {
      if (action.turnScheduled === gameState.currentTurn && !action.resolved) {
        switch (action.type) {
          case 'attack': newEnemyHealth = Math.max(0, newEnemyHealth - action.value); break;
          case 'heal': newPlayerHealth = Math.min(gameState.maxPlayerHealth, newPlayerHealth + action.value); break;
        }
        return { ...action, resolved: true };
      }
      return action;
    });

    // 2. Resolve Enemy Actions for current turn
    newEnemyTimeline = newEnemyTimeline.map(action => {
      if (action.turnScheduled === gameState.currentTurn && !action.resolved) {
        switch (action.type) {
          case 'attack': newPlayerHealth = Math.max(0, newPlayerHealth - action.value); break;
          // ... other enemy action types
        }
        return { ...action, resolved: true };
      }
      return action;
    });
    
    // 3. Enemy AI: Plan next action (simple)
    if (gameState.currentTurn % 2 === 0 && newEnemyHealth > 0) { // Enemy acts every 2 turns
      const enemyAction: TimelineAction = {
        id: createId(),
        turnScheduled: gameState.currentTurn + Math.floor(Math.random() * 2) + 1, // Schedules 1 or 2 turns ahead
        description: 'Enemy Attack',
        actor: 'enemy',
        type: 'attack',
        value: 5 + Math.floor(Math.random() * 10), // Random damage between 5-14
        icon: '💀',
        resolved: false,
      };
      newEnemyTimeline.push(enemyAction);
    }
    
    // 4. Check game over
    let newGameStatus = gameState.gameStatus;
    if (newPlayerHealth <= 0) newGameStatus = 'enemy_won';
    else if (newEnemyHealth <= 0) newGameStatus = 'player_won';

    // 5. Update state
    // Use a timeout to simulate processing and allow animations to be perceived
    setTimeout(() => {
      setGameState(prev => ({
        ...prev,
        playerHealth: newPlayerHealth,
        enemyHealth: newEnemyHealth,
        playerTimeline: newPlayerTimeline.filter(a => !a.resolved || a.turnScheduled > prev.currentTurn), // Clean up resolved past actions
        enemyTimeline: newEnemyTimeline.filter(a => !a.resolved || a.turnScheduled > prev.currentTurn),
        currentTurn: prev.currentTurn + 1,
        isProcessingTurn: false,
        gameStatus: newGameStatus,
        history: [...prev.history, snapshot], // Add PREVIOUS state to history
        selectedPiece: null, // Clear selection after turn
         // Refill grid (very basic, no gravity or cascades)
        puzzleGrid: newGameStatus === 'playing' ? prev.puzzleGrid.map(row => row.map(p => Math.random() < 0.1 ? generateRandomPiece() : p )) : prev.puzzleGrid,
      }));
    }, 700); // Animation/processing delay

  }, [gameState]);

  // --- Replay Logic ---
  const startReplay = () => {
    if (gameState.history.length === 0) return;
    setGameState(prev => ({
      ...prev,
      replayMode: true,
      replayTurnIndex: 0, // Start from the beginning
      ...loadStateFromSnapshot(prev.history[0]), // Load first state
    }));
  };

  const exitReplay = () => {
    // Restore to the latest game state
    const latestSnapshot = gameState.history[gameState.history.length -1] || createInitialSnapshot();
    setGameState(prev => ({
      ...prev,
      replayMode: false,
      ...loadStateFromSnapshot(latestSnapshot), // Load latest state
      currentTurn: latestSnapshot.currentTurn +1, // Game continues from next turn of last recorded state
      history: prev.history, // Retain history
    }));
  };

  const replayStep = (direction: 'next' | 'prev') => {
    if (!gameState.replayMode) return;
    let newIndex = gameState.replayTurnIndex;
    if (direction === 'next' && gameState.replayTurnIndex < gameState.history.length - 1) {
      newIndex++;
    } else if (direction === 'prev' && gameState.replayTurnIndex > 0) {
      newIndex--;
    }
    setGameState(prev => ({
      ...prev,
      replayTurnIndex: newIndex,
      ...loadStateFromSnapshot(prev.history[newIndex]),
    }));
  };

  const loadStateFromSnapshot = (snapshot: GameStateSnapshot): Partial<GameState> => ({
    currentTurn: snapshot.currentTurn,
    playerHealth: snapshot.playerHealth,
    enemyHealth: snapshot.enemyHealth,
    puzzleGrid: snapshot.puzzleGrid.map(row => row.map(p => ({...p}))), // Deep copy snapshot grid
    playerTimeline: snapshot.playerTimeline.map(a => ({...a})),
    enemyTimeline: snapshot.enemyTimeline.map(a => ({...a})),
    gameStatus: snapshot.gameStatus,
  });

  const createInitialSnapshot = (): GameStateSnapshot => ({
    currentTurn: 1,
    playerHealth: INITIAL_PLAYER_HEALTH,
    enemyHealth: INITIAL_ENEMY_HEALTH,
    puzzleGrid: initializeGrid(),
    playerTimeline: [],
    enemyTimeline: [],
    gameStatus: 'playing',
  });

  // --- Render ---
  const mainContentLayoutStyle = gameState.isMobileView
    ? "flex-col space-y-4"
    : "flex space-x-4";
  
  const puzzlePanelStyle = gameState.isMobileView 
    ? { width: '100%' } 
    : { width: `${gameState.leftPanelWidth}%` };
  
  const abilityPanelStyle = gameState.isMobileView 
    ? { width: '100%' } 
    : { width: `${100 - gameState.leftPanelWidth - 2}%` }; // 2% for splitter approx.

  return (
    <div className={`min-h-screen ${COLORS.bgVeryDark} ${COLORS.textLight} flex flex-col items-center p-4 font-sans transition-all duration-300`}>
      <div style={{ width: gameState.isMobileView ? '100%' : DESKTOP_FIXED_WIDTH }} className="flex flex-col space-y-4">
        
        {/* Header: Game Info & Replay Controls */}
        <header className={`flex justify-between items-center p-4 ${COLORS.bgPanel} rounded-lg ${COLORS.shadowColor}`}>
          <h1 className="text-2xl font-bold text-[#F2EB80]">Puzzle Battle Arena</h1>
          {gameState.replayMode ? (
            <div className="flex items-center space-x-2">
              <button onClick={() => replayStep('prev')} disabled={gameState.replayTurnIndex === 0} className={`px-3 py-1 ${COLORS.accentOrangeBrown} rounded hover:bg-[#8C441B] disabled:opacity-50`}>Prev</button>
              <span className="text-sm">Turn: {gameState.currentTurn} (Replay)</span>
              <button onClick={() => replayStep('next')} disabled={gameState.replayTurnIndex === gameState.history.length - 1} className={`px-3 py-1 ${COLORS.accentOrangeBrown} rounded hover:bg-[#8C441B] disabled:opacity-50`}>Next</button>
              <button onClick={exitReplay} className={`px-3 py-1 bg-red-600 hover:bg-red-700 rounded`}>Exit Replay</button>
            </div>
          ) : (
            <div className="flex items-center space-x-2">
                <span className="text-lg">Turn: {gameState.currentTurn}</span>
                <button onClick={startReplay} disabled={gameState.history.length === 0} className={`px-3 py-1 ${COLORS.accentOrangeBrown} rounded hover:bg-[#8C441B] disabled:opacity-50`}>Replay</button>
            </div>
          )}
        </header>

        {/* Health Bars */}
        <div className={`grid ${gameState.isMobileView ? 'grid-cols-1 gap-2' : 'grid-cols-2 gap-4'} p-4 ${COLORS.bgPanel} rounded-lg ${COLORS.shadowColor}`}>
          <HealthBar label="Player" currentHealth={gameState.playerHealth} maxHealth={gameState.maxPlayerHealth} barColor="bg-green-500" />
          <HealthBar label="Enemy" currentHealth={gameState.enemyHealth} maxHealth={gameState.maxEnemyHealth} barColor="bg-red-500" />
        </div>

        {/* Main Game Area: Puzzle Grid + Ability Bar (with Splitter) */}
        <div className={`flex ${mainContentLayoutStyle} w-full`}>
          {/* Puzzle Grid Area */}
          <div ref={puzzleGridRef} style={puzzlePanelStyle} className={`p-4 ${COLORS.bgPanel} rounded-lg ${COLORS.shadowColor} transition-width duration-100 ease-linear`}>
            <h2 className="text-xl font-semibold mb-3 text-center">Puzzle Grid</h2>
            <div className="grid gap-1 mx-auto" style={{gridTemplateColumns: `repeat(${GRID_SIZE}, minmax(0, 1fr))`, aspectRatio: '1 / 1', maxWidth: '500px'}}>
              {gameState.puzzleGrid.map((row, r) =>
                row.map((piece, c) => {
                  const isSelected = gameState.selectedPiece?.r === r && gameState.selectedPiece?.c === c;
                  const isInCombo = gameState.comboChain.some(p => p.r === r && p.c === c);
                  return (
                    <div
                      key={`${r}-${c}-${piece.id}`}
                      className={`aspect-square ${piece.color} rounded flex items-center justify-center 
                                  cursor-pointer transition-all duration-150 
                                  ${isSelected ? 'ring-4 ring-offset-2 ring-[#F2EB80] ring-offset-[#401A04]' : ''}
                                  ${isInCombo ? 'animate-pulse scale-110 shadow-lg' : ''}
                                  hover:scale-105`}
                      onClick={() => handlePieceClick(r, c)}
                      style={{ animationDuration: isInCombo ? '300ms' : undefined }}
                    >
                      {/* Piece representation (e.g. icon or symbol) could go here */}
                      {/* <span className="text-xs text-black font-bold">{piece.type}</span> */}
                    </div>
                  );
                })
              )}
            </div>
          </div>

          {/* Draggable Splitter (only on desktop) */}
          {!gameState.isMobileView && (
            <div ref={splitterRef} className="w-2 cursor-ew-resize flex items-center justify-center group">
              <div className={`h-1/2 w-1 ${COLORS.borderMediumBrown} group-hover:bg-[#F2EB80] rounded-full transition-colors`}></div>
            </div>
          )}

          {/* Ability Bar Area */}
          <div ref={abilityBarRef} style={abilityPanelStyle} className={`p-4 ${COLORS.bgPanel} rounded-lg ${COLORS.shadowColor} flex flex-col space-y-3 transition-width duration-100 ease-linear`}>
            <h2 className="text-xl font-semibold mb-2 text-center">Abilities</h2>
            {playerAbilities.map(ability => (
              <button
                key={ability.id}
                onClick={() => useAbility(ability.id)}
                disabled={gameState.isProcessingTurn || gameState.replayMode || gameState.gameStatus !== 'playing'}
                className={`w-full p-3 ${COLORS.accentOrangeBrown} rounded ${COLORS.textLight} font-medium 
                            hover:bg-[#8C441B] disabled:opacity-50 disabled:cursor-not-allowed
                            transition-all duration-150 ease-in-out ${COLORS.shadowColor}`}
              >
                {ability.name}
              </button>
            ))}
          </div>
        </div>

        {/* Timelines Area */}
        <div className={`flex ${gameState.isMobileView ? 'flex-col space-y-4' : 'space-x-4'} w-full`}>
          <Timeline 
            actions={gameState.playerTimeline} 
            currentTurn={gameState.currentTurn} 
            title="Player Actions" 
            isPlayerTimeline={true}
            onHoverAction={(action) => setGameState(prev => ({ ...prev, previewedAction: action }))}
          />
          <Timeline 
            actions={gameState.enemyTimeline} 
            currentTurn={gameState.currentTurn} 
            title="Enemy Actions" 
            isPlayerTimeline={false}
            onHoverAction={(action) => setGameState(prev => ({ ...prev, previewedAction: action }))}
          />
        </div>

        {/* Action Preview Tooltip */}
        {gameState.previewedAction && (
          <div 
            className={`fixed bottom-4 right-4 p-3 ${COLORS.bgPanel} ${COLORS.borderMediumBrown} border rounded-lg ${COLORS.shadowColor} z-50 max-w-xs`}
            style={{ pointerEvents: 'none' }} // So it doesn't interfere with mouse events on elements below
          >
            <h4 className={`${COLORS.textLight} font-semibold`}>Preview: {gameState.previewedAction.actor}'s Action</h4>
            <p className="text-sm">{gameState.previewedAction.description}</p>
            <p className="text-sm">Type: {gameState.previewedAction.type}, Value: {gameState.previewedAction.value}</p>
            <p className="text-sm">Scheduled for Turn: {gameState.previewedAction.turnScheduled}</p>
          </div>
        )}

        {/* Game Over Message */}
        {gameState.gameStatus !== 'playing' && (
          <div className={`fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50`}>
            <div className={`p-8 ${COLORS.bgPanel} rounded-lg ${COLORS.shadowColor} text-center`}>
              <h2 className="text-3xl font-bold mb-4">
                {gameState.gameStatus === 'player_won' ? '🎉 You Won! 🎉' : '💀 Game Over 💀'}
              </h2>
              <button
                onClick={() => {
                  const initialSnapshot = createInitialSnapshot();
                  setGameState(prev => ({
                    ...prev,
                    ...initialSnapshot,
                    maxPlayerHealth: INITIAL_PLAYER_HEALTH, // ensure these are reset
                    maxEnemyHealth: INITIAL_ENEMY_HEALTH,
                    isMobileView: window.innerWidth < MOBILE_BREAKPOINT,
                    leftPanelWidth: 60,
                    history: [], // Clear history for new game
                    replayMode: false,
                  }));
                }}
                className={`px-6 py-3 ${COLORS.accentOrangeBrown} rounded ${COLORS.textLight} font-semibold text-lg hover:bg-[#8C441B]`}
              >
                Play Again
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default PuzzleBattleGame;